.ui-radio {
    font-size: 14px;
    padding: 6px 15px;
    border-radius: 25px;
    cursor: default;
    outline: none;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.ui-radio:hover,
.ui-radio:focus {
    background-color: #97be60;
    color: #fff;
    -webkit-box-shadow: 0 0 10px 0 rgba(151, 190, 96, 1);
            box-shadow: 0 0 10px 0 rgba(151, 190, 96, 1);
}
.ui-radio[aria-checked="true"] {
    background-color: #97be60;
    color: #fff;
}


